import React from 'react';
import s from "./services.module.css"
import PageTitle from "../UI/PageTitle";
import {CardService} from "../UI/Card_service/CardService";

const Services = () => {
    const description = "    ,  ,    " +
        "   ,  -,    " +
        "    ."

    const data = [
        {
            service: " ",
            description: "    .",
            price: "5000"
        },
        {
            service: " ",
            description: "       .",
            price: "12000"
        },
        {
            service: " ",
            description: "    ,      " +
                " .",
            price: "32000"
        },
    ]
    return (
        <section id={"services"}>
            <div className={s.container}>

                {/*--- Main Title ---*/}
                <PageTitle subTitle={""} title={"  "} description={description}/>
                <div className={s.service__list}>

                    {/*--- My services & Price---*/}
                    {
                        data.map((item, i) => {
                            return <CardService
                                key={i}
                                service={item.service}
                                description={item.description}
                                price={item.price}
                            />
                        })
                    }

                </div>
            </div>
        </section>
    );
};

export default Services;