import {TextField} from "@mui/material";
import {BigButton} from "../UI/BigButton/BigButton";
import PageTitle from "../UI/PageTitle";
import s from "./contact.module.css";
import {useForm} from "react-hook-form";
import axios, {AxiosRequestConfig} from "axios";

const Contact = () => {
    const description =
        `"    - ,      ,
     ,   ,       ."`;

    const {register, handleSubmit,} = useForm();


    const onSubmit = (data: any) => {
        console.log(data)
        axios.post("https://smtp--server.herokuapp.com/sendMail", {
                name: data.name,
                email: data.email,
                phone: data.phone,
                message: data.message
            }
        )
            .then(() => alert("message has been sent"))
    }

    return (
        <section id={"contact"}>
            <div className={s.container}>
                <PageTitle
                    title={""}
                    subTitle={""}
                    description={description}
                />
                <div className={s.form}>
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <div className={s.textField}>
                            <TextField
                                id="outlined-basic"
                                label=" *"
                                variant="outlined"
                                sx={{padding: "0px 15px 15px 0px", width: "100%"}}
                                {...register("name", {required: true})}
                            />
                            <TextField
                                id="outlined-basic"
                                label="Email*"
                                variant="outlined"
                                sx={{padding: "0px 0px 15px 0px", width: "100%"}}
                                {...register("email", {required: true})}
                            />
                        </div>

                        <TextField
                            id="outlined-basic"
                            label="*"
                            variant="outlined"
                            sx={{padding: "0px 0px 15px 0px", width: "100%"}}
                            {...register("phone", {required: true})}
                        />
                        <TextField
                            id="outlined-multiline-static"
                            label=""
                            multiline
                            rows={6}
                            sx={{padding: "0px 0px 15px 0px", width: "100%"}}
                            {...register("message", {required: true})}
                        />
                        <BigButton> </BigButton>
                        <button type="submit">SEND</button>
                    </form>
                </div>

                <div className={s.contact_info}>
                    <p></p>
                    <h3>, </h3>
                    <p></p>
                    <h3>
                        <a href="tel:+79772641610">(+7) 977 264 1610</a>
                    </h3>
                    <p>
                        <a className={s.link_email} href="mailto:yurakholoimov@ya.ru">
                            yurakholoimov@ya.ru
                        </a>
                    </p>
                </div>
            </div>
        </section>
    );
};

export default Contact;
