import * as React from 'react';
import {CardAbout} from "../../UI/CardAbout/CardAbout";

type Props = {};
export const Education = (props: Props) => {
    const data = [
        {
            position: '"   ,   (  )"',
            title: ', ',
            date: '(2021 - ..)',
            description: '',
        },
        {
            position: '   ',
            title: ', ',
            date: '(2005 - 2009)',
            description: '   "   ",  ',
        },
        {
            position: '   ',
            title: ' ',
            date: '(2001 - 2005)',
            description: '   "   ",  ',
        },
    ]

    return (
        <div>
            {
                data.map((item , index)=> {
                    return (
                        <CardAbout
                            key={index}
                            position={item.position}
                            title={item.title}
                            date={item.date}
                            description={item.description}
                        />)
                })
            }

        </div>
    );
};