import React from 'react';
import s from "./experience.module.css";
import {CardAbout} from "../../UI/CardAbout/CardAbout";

const Experience = () => {
    const data = [
        {
            position: 'Front-end ',
            title: 'Mobirise.com',
            date: '(2021 - ..)',
            description: '   ,       ,  React TS, JavaScript, CSS.   .    JSX-  hook-o,    .\n' +
                '       ,\n' +
                'Unit-testing .',
        },
        {
            position: 'Front-end ',
            title: 'Freelance',
            date: '(2005 - 2009)',
            description: '         React TS/ Redux  Material UI;   ;   ,   CRM.  React    , HOC   .   ,    ',
        },
    ]
    return (
        <div className={s.experienceContainer}>
            {
                data.map((item, index) => {
                    return (
                        <CardAbout
                            key={index}
                            position={item.position}
                            title={item.title}
                            date={item.date}
                            description={item.description}
                        />)
                })
            }
        </div>
    );
};

export default Experience;